	import btncoms from "../../components/btncom.vue"
	import service from "../../service.js";
	export default{
		components:{
			 btncoms
		},
		data:function(){
			return{
				// Htitle:"申请救济",
				// Hlicon:"../../static/icon/left_whitch.png",
				bitstyle:"margin-top:50upx",
				btntitle:"兑换",
				bitcolor:"",
				clause:true,
				codeTime:'获取验证码',
				wti:'',
				password:'',
				verifycode:'',
				codeB:true,
				account:uni.getStorageSync('member').account
			}
		},
		methods:{
			btns(){
				service.auth(this , service.api.index.ex_stock , "POST" , {
					wti:this.wti,
					password:this.password,
					verifycode:this.verifycode,
				},function(self,res){
					// console.log(res);
					uni.showToast({
						title:res.info,
						icon:'none'
					});
					
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},1500)

				})
			},
			jump(e){
				uni.switchTab({
					url:e
				});
			},
			jum(url){
				uni.navigateTo({
					url
				});
			},
			getAuthCode(){
                var self = this;
                if(self.codeB){
                        self.codeB = false;
                        self.codeTime = 60
						uni.request({
							url: service.api.index.verifycode,
							method: 'GET',
							data: {},
							header: {
								Authorization: 'Bearer ' + uni.getStorageSync('token')
							},
							success: res => {
								// console.log(res)
								if (res.code == 1) {
									uni.showToast({
										icon: 'none',
										title: res.data.info
									});
								} else {
									uni.showToast({
										icon: 'none',
										title: res.data.info
									});
									return;
								}
							}
						});
                if(self.codeTime > 0){
                        var sss = setInterval(()=>{
                                self.codeTime--;
                                if(self.codeTime == 0){
                                   self.codeTime = '重新获取验证码'
                                   clearInterval(sss);
                                   self.codeB = true;     
                                }
                        },1000)
                }else{
                        self.codeTime = '重新获取验证码'
                        self.codeB = true;
                }
                }else{
                        console.log('12321')
                        uni.showToast({
                                title:'请勿重复获取验证码!',
                                icon:'none'
                        })
                }                
                                
            }
		}
	}
